export default class Faq {
  constructor() {
    this.accordionHead = '.accordion__head';

    this.componentBuilder();
  }

  handleAccordionClick() {
    const items = document.querySelectorAll(this.accordionHead);

    items.forEach(item => {
      item.addEventListener('click', function() {
        items.forEach(item =>
          item.parentNode.classList.remove('accordion__item--active')
        );

        item.parentNode.classList.add('accordion__item--active');
      });
    });
  }

  componentBuilder() {
    document.addEventListener('DOMContentLoaded', event => {
      this.handleAccordionClick();
      this.setHandledContactButtonClick();
    });
  }

  setHandledContactButtonClick() {
    const contactButton = document.querySelector('.contact__button');

    contactButton.addEventListener('click', e => {
      e.preventDefault();

      const jivoChatButton = document.querySelector(
        '#jivo-label-buttons .chat_2j'
      );

      if (jivoChatButton) {
        const eventClick = new Event('click', {
          bubbles: true,
        });

        jivoChatButton.dispatchEvent(eventClick);
      }
    });
  }
}

new Faq();
